import { useEffect, useState } from "react";
import Axios from "axios";
import "./styles.css";
import { Button, Typography } from "@material-ui/core";
import BasicTable from "./BasicTable";

function App() {
  // Setting up the initial state variables
  const [ipDetails, setIpDetails] = useState([]);
  const [lat, setLat] = useState();
  const [lon, setLon] = useState();

  // Fetching the API once when the
  // component is mounted
  useEffect(() => {
    Axios.get("https://ipapi.co/json/").then((res) => {
      setIpDetails(res.data);
      setLat(res.data.latitude);
      setLon(res.data.longitude);
    });
  }, []);

  //Banking
  const [open, setOpen] = useState(false);

  const handleBank = () => {
    setOpen(!open);
  };
  return (
    <>
      <h2 className="heading">IP Address </h2>
      <div className="App">
        <div className="left">
          <h1 id="ip">{ipDetails.ip}</h1>
          <h4>Approximate location: </h4>

          <p>
            {ipDetails.city}, {ipDetails.region},{ipDetails.country_name}.
          </p>
          <h4>Internet Service Provider(ISP):</h4>
          <p>{ipDetails.org}</p>
          <p> Latitude : {lat} </p>
          <p>Longitude : {lon} </p>
        </div>
        <Button variant="contained" onClick={() => setOpen(!open)} fullWidth>
          {!open ? "Open Bank" : "Close Bank"}
        </Button>
        {open === true ? (
          <>
            <h3> Welcome to Lena Bank, take unlimited LOAN </h3>
            <BasicTable />
          </>
        ) : (
          ""
        )}
      </div>
    </>
  );
}

export default App;
